body{
    font-family: Verdana, Geneva, sans-serif;
    color: #292b2c;

    a {
        color:#292b2c;
        text-decoration: none;
        &:visited{
            color:#292b2c;
        }
    }
}
h1 {
    font-size: 30px;
    font-family: sans-serif;
}

table {
    border-collapse: collapse;
}

table, th, td {
    border: 1px solid #292b2c;
}

th {
    background-color: #292b2c;
    color: white;
}

th, td {
    padding: 5px;
    text-align: left;
}

tr:nth-child(even) {background-color: #f2f2f2}
